export default {
  name: 'MpSearch',
  props: {
    confirmType: {
      type: String,
      default: 'done',
    },
    placeholder: {
      type: String,
      default: '搜索',
    },
    cancelText: {
      type: String,
      default: '取消',
    },
    value: String,
  },
  data() {
    return {
      currentValue: this.value,
      visible: false,
    };
  },
  watch: {
    currentValue(value) {
      this.$emit('input', value);
      this.$emit('change', value);
    },
    value(value) {
      this.currentValue = value;
    },
  },
};
